.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
}

.product>img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}

.product>button {
    background: #f0c14b;
    border: none;
    margin-top: 10px;
    color: #111;
    padding: 6px 15px;
}

.product_info {
    height: 100px;
    margin-bottom: 15px;
    width: 100%;
}

.product_price {
    margin-top: 5px;
}

.product_rating {
    display: flex;
}

.rating_star {
    color: yellow;
}